@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    background-color: #282a36;
}
.App {
    margin-top: 70px;
    display: flex;
    justify-content: center;
}

.TodoTemplate{width:40%;height:85vh;border-radius:15px;background-color:#44475a;font-family:"Noto Sans KR", sans-serif}.TodoTemplate_header{display:flex;flex-direction:column}.TodoTemplate_header_mainTitle{margin:10px;text-align:center;font-size:40px;font-weight:bold;color:#f8f8f2}.TodoTemplate_header_subTitle{text-align:center;color:#bd93f9;font-weight:bold}.TodoTemplate_body{display:flex;justify-content:center;align-items:center;flex-direction:column;height:70%;overflow:auto}.TodoTemplate_footer{display:flex;justify-content:center;align-items:center;flex-direction:column}.TodoTemplate_footer_input{margin-top:10px;display:flex;flex-direction:column}li{list-style:none;font-size:20px}.todo{width:500px;box-shadow:0 0 8px rgba(0,0,0,0.44);border-radius:10px;font-weight:bold;padding:30px;color:#f8f8f2;display:flex;align-items:center;margin:10px;justify-content:space-between}.todo_input{font-family:"Noto Sans KR", sans-serif;font-weight:bold;padding:10px;font-size:20px;outline:none;width:500px;border:none;border-radius:10px}.todo_submit{padding:5px;font-family:"Noto Sans KR", sans-serif;font-weight:bold;font-size:30px;color:#bd93f9;background-color:#282a36;outline:none;border:none;border-radius:10px;cursor:pointer}.todo_delete-all{border-radius:10px;background-color:#bd93f9;outline:none;border:none;font-weight:bold;font-family:"Noto Sans KR", sans-serif;cursor:pointer}.delete{cursor:pointer}

